import React, { Component } from 'react';
import './style.css'
import api from '../../api'
import Loading from '../../components/Loading'

class Faq extends Component {
    constructor(props) {
        super(props)
        this.state = {
            minHeight: window.innerHeight - 141,
            loading: true,
            tab: 0,
            cycleTime:15,
            timeId:null
        }
    }
    componentDidMount() {
        let _this = this;
        fetch(api.url + 'MoniterInfo').then(res => res.json()).then((data) => {
            if(data.message && data.message=='error'){
                alert(data.messageContent);
                return
            }
            this.setState({
                address: data.address,
                threads: data.Threads,
                tcpsum: data.tcpsum
            })
            Promise.all([this.getMoniterShare(), this.getMoniterHash()]).then(() => {
                _this.setState({
                    loading: false
                })
            })
        })
        this.setState({
            timeId:setInterval(()=>{
                Promise.all([this.getMoniterShare(), this.getMoniterHash()]).then(() => {
                    
                })
            },this.state.cycleTime*1000)
        })
        window.onresize = function () {
            _this.setState({ minHeight: window.innerHeight - 141 })
        }
    }
    getMoniterShare = () => {
        let _this = this
        return new Promise((resolve, reject) => {
            fetch(api.url + 'MoniterShares?address=' + _this.state.address).then(res => res.json()).then((data) => {
                this.setState({
                    shareData: data
                })
                resolve()
            })
        })
    }

    getMoniterHash = () => {
        let _this = this
        return new Promise((resolve, reject) => {
            fetch(api.url + 'MoniterHash?address=' + _this.state.address).then(res => res.json()).then((data) => {
                this.setState({
                    HashData: data
                })
                resolve()
            })
        })
    }
    setCycleTime = (t) => {
        this.setState({
            cycleTime: t
        })
    }
    changeCycle=()=>{
        clearInterval(this.setState.timeId)
        this.setState({
            timeId:setInterval(()=>{
                Promise.all([this.getMoniterShare(), this.getMoniterHash()]).then(() => {
                    
                })
            },this.state.cycleTime*1000)
        })
    }
    render() {
        return (
            <div className="Faq-container" style={{ minHeight: this.state.minHeight }}>
                <Loading loading={this.state.loading} />
                <div className='container'>
                    <div className='Moniter-info clearfix'>
                        <div className='left'>
                            <div className='address'>当前监视地址：{this.state.address}</div>
                        </div>

                        <div className='right'>
                            <div className='threads'><span className='space'>PoolWorker线程数：</span><span className='space'>{this.state.threads}</span></div>
                            <div className='tcpsum'><span className='space'>监视地址TCP追踪：</span><span className='space'>{this.state.tcpsum}</span></div>
                        </div>
                    </div>
                    <div className='Moniter-detail'>
                        <div className='switcher'>
                            <span onClick={() => { this.setState({ tab: 0 }) }} className={this.state.tab == 0 ? 'active moniter-type' : 'moniter-type'}>share监视器</span>
                            <span onClick={() => { this.setState({ tab: 1 }) }} className={this.state.tab == 1 ? 'active moniter-type' : 'moniter-type'}>hash监视器</span>
                            <div className='cycle'>
                                <span className='tip'>数据自动刷新时间:</span>
                                <input type="number" className='cycleTime' value={this.state.cycleTime} onChange={(e)=>{this.setCycleTime(e.target.value)}} onKeyPress={this.changeCycle}/>
                                <span className='tip'>秒</span>
                            </div>

                        </div>
                        {this.state.tab == 0 ? <table className='share-table'>
                            <thead>
                                <tr>
                                    <th>矿机编号</th>
                                    <th>jobId</th>
                                    <th>Nonce</th>
                                    <th>share哈希</th>
                                    <th>端口难度</th>
                                </tr>

                            </thead>
                            <tbody>
                                <ShareList dataSource={this.state.shareData || []} />
                            </tbody>
                        </table> : null}
                        {this.state.tab == 1 ? <table className='hash-table'>
                            <thead>
                                <tr>
                                    <th>矿机编号</th>
                                    <th>jobId</th>
                                    <th>extraNonce1</th>
                                    <th>Nonce</th>
                                    <th>share哈希</th>
                                    <th>头部Buffer</th>
                                </tr>

                            </thead>
                            <tbody>
                                <HashList dataSource={this.state.HashData || []} />
                            </tbody>
                        </table> : null}
                    </div>



                </div>

            </div>)
    }
}

export default Faq
function ShareList(props) {
    return (
        props.dataSource.map((e, i) => {
            let data = JSON.parse(e)
            return (<tr key={i}>
                <td>{data.name.split('.')[1]}</td>
                <td>{data.jobId}</td>
                <td>{data.nonce}</td>
                <td>{data.hash}</td>
                <td>{data.clientDiff}</td>
            </tr>)
        })
    )
}
function HashList(props) {
    return (
        props.dataSource.map((e, i) => {
            let data = JSON.parse(e)
            return (<tr key={i}>
                <td>{data.workerName.split('.')[1]}</td>
                <td>{data.jobId}</td>
                <td>{data.extraNonce1}</td>
                <td>{data.extraNonce2}</td>
                <td>{data.submitHash}</td>
                <td>{data.headerBuffer}</td>
            </tr>)
        })
    )
}


